var config = {
	type: Phaser.AUTO,
	width: 800,
	height: 600,
	// physics: {
	// 	default: 'arcade',
	// 	arcade: {
	// 		gravity: {
	// 			y: 200
	// 		}
	// 	}
	// },
	scene: {
		preload: preload,
		create: create
	}
};

var game = new Phaser.Game(config);

function preload() {
	game.load.image('ground-plates', '../assets/tilemaps/ground-plates.png');
	game.load.tilemapTiledJSON('first', '../assets/tilemaps/first.json');

}

function create() {
	game.add.tilemap('first')

}